<template>
    <div class="prize">
        <div class="prize-head">
            <div class="right">
                <el-select class="search" v-model="messageType" size="small"
                           @change="changeTypeFunc" v-if="$route.params.hasOwnProperty('type') && $route.params.type !== 'point' && $route.params.type !== 'coupon'">
                    <el-option
                            v-for="item in messageTypelist"
                            :key="item.type"
                            :label="item.name"
                            :value="item.type">
                    </el-option>
                </el-select>
                <span class="font-middle">{{ $L('获奖时间') }}：</span>
                <el-date-picker class="time-search"
                                style="width: 350px;"
                                v-model="SearchTimeZone"
                                type="datetimerange"
                                align="right"
                                size="small"
                                :start-placeholder="$L('开始日期')"
                                :end-placeholder="$L('结束日期')"
                                value-format="yyyy-MM-dd HH:mm:ss"
                                :default-time="['00:00:00', '23:59:59']"
                                @change="SearchTimeFunc">
                </el-date-picker>
            </div>
        </div>
        <div class="prize-content">
            <div class="prizeList">
                <el-table
                        :data="tableData.list"
                        :empty-text="$L('暂无数据')"
                        :header-cell-style="{background:'#f0f1f5',color:'#666'}"
                        style="width: 100%" class="table">
                    <el-table-column
                            prop="info"
                            :label="$L('奖品信息')"
                            align="left" width="500">
                        <div slot-scope="scope">
                            <img src="../../../assets/img/prize/jifen.png" alt="" class="img" v-if="$route.params.hasOwnProperty('type') && $route.params.type == 'point'">
                            <img src="../../../assets/img/prize/points_coupon.png" alt="" class="img" v-else-if="$route.params.hasOwnProperty('type') && $route.params.type == 'coupon'">
                            <img :src="scope.row.pic" alt="" class="img" v-else>
                            <div class="textBox">
                                <p class="orderColor">{{ scope.row.name }}</p>
                                <p class="gray">{{$L('订单号')}}：{{ scope.row.sn }}</p>
                            </div>
                        </div>
                    </el-table-column>
                    <el-table-column
                            prop="time"
                            :label="$L('时间')"
                            align="center">
                        <div slot-scope="scope" class="gray">
                            {{ scope.row.time }}
                        </div>
                    </el-table-column>
                    <el-table-column
                            prop="source"
                            :label="$L('来源')"
                            align="center">
                        <div slot-scope="scope">
                            <p>{{ fromTypeFunc(scope.row.from).text }}</p>
                            <p v-if="scope.row.type == 1">{{ $L('礼品') }}</p>
                            <p v-else-if="scope.row.type == 2">{{ $L('优惠券') }}</p>
                            <p v-else-if="scope.row.type == 3">{{ $L('积分') }}</p>
                            <p v-else>{{ $L('产品') }}</p>
                        </div>
                    </el-table-column>
                    <el-table-column
                            prop="status"
                            :label="$L('状态')"
                            align="center" v-if="$route.params.hasOwnProperty('type') && $route.params.type !== 'point' && $route.params.type !== 'coupon'">
                        <div slot-scope="scope">
                            <p v-bind:class="stateTypeFunc(scope.row.deliveryStatus,scope.row.status).color">{{stateTypeFunc(scope.row.deliveryStatus,scope.row.status).text}}</p>
                        </div>
                    </el-table-column>
                    <el-table-column
                            prop="handle"
                            :label="$L('操作')"
                            align="center">
                        <div slot-scope="scope">
                            <el-button type="primary" v-if="scope.row.status == 0 && scope.row.type !== 2 && scope.row.type !== 3" @click="applyFunc(scope.row.id,scope.row.type,scope.row.from,scope.row.prizeRecordId,scope.row.product_type)">{{ $L('申请领奖') }}</el-button>
                            <el-button v-if="scope.row.deliveryStatus == 2" type="primary" v-on:click="confirmFunc(scope.row.id)">{{$L('确认收货')}}</el-button>
                            <span v-else-if="scope.row.status == 1  && scope.row.type !== 2 && scope.row.type !== 3">
                                <p class="details mainFontColor" @click="getInfoFunc(scope.row.id,scope.row.product_type)">{{ $L('订单详情') }}</p>
                            </span>
                            <el-button type="primary" v-if="scope.row.type === 2" @click="goCoupon">{{ $L('查看优惠券') }}</el-button>
                            <el-button type="primary" v-if="scope.row.type === 3" @click="goPoint">{{ $L('查看积分') }}</el-button>
                        </div>
                    </el-table-column>
                </el-table>
            </div>
            <!-- 分页 -->
            <div class="list-pages" v-if="tableData.list !== null && tableData.allNum>0">
                <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[10,20,30,40]"
                        :page-size="pageSize"
                        layout="prev, pager, next, total, jumper"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    import exchangeBox from "../../../components/prize/exchangeBox.vue";
    export default {
        name: "prize",
        components: {exchangeBox},
        data(){
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                value1: '',
                SearchTimeZone:'',
                messageType:'all',
                messageTypelist:[
                    {
                        type:'all',
                        name:this.$L('全部状态')
                    },
                    {
                        type:'applyPrize',
                        name:this.$L('未申请领奖')
                    },
                    {
                        type:'picking',
                        name:this.$L('等待卖家发货')
                    },
                    {
                        type:'noFinish',
                        name:this.$L('卖家已发货，等待确认')
                    },
                    {
                        type:'finish',
                        name:this.$L('订单已完成')
                    },
                ],
                dtCreateStart:'',
                dtCreateEnd:'',
                tableData:{},
                currentPage:1,
                pageSize:20,
            }
        },
        props:{

        },
        beforeRouteUpdate (to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.getData(to);
            next();
        },
        created() {
            this.getData();
        },
        methods:{
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                if(!router){
                    router = _self.$route;
                }
                _self.loading = true;
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    status : router.params.type ? router.params.type :'prod',
                    dtCreateStart: _self.dtCreateStart,
                    dtCreateEnd: _self.dtCreateEnd,
                    type : _self.messageType
                };
                _self.$api('getPrizeList', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            goCoupon(){
                this.$router.push({name:"couponList"});
            },
            goPoint(){
                this.$router.push({name:"integralPoint"});
            },
            getInfoFunc(id,ptype){
                this.$router.push({name:"prizeInfo",params:{id:id,ptype:ptype}});
            },
            changeTypeFunc(val){
                this.messageType = val;
                this.getData();
            },
            searchFunc(val){
                this.inputValue = val;
                this.getData();
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            handleSelectionChange(val){
                let arr = [];
                for (let i in val){
                    arr[i] = val[i].id;
                }
                this.msgArr = arr;
            },
            stateTypeFunc(delivery,order){
                let s;
                if(order == -1){
                    s = {text:this.$L("订单已关闭"),color:'green'}
                }else if(delivery == 3){
                    s = {text:this.$L("订单已完成"),color:'green'}
                }else if(delivery == 2){
                    s = {text:this.$L("已发货"),color:'green'}
                }else if(delivery == 1){
                    s = {text:this.$L("配货中"),color:'green'}
                }else if(delivery == 0 && order == 0){
                    s = {text:this.$L("未申请领取"),color:'red'}
                }else if(delivery == 0 && order == 1){
                    s = {text:this.$L("等待配货"),color:'green'}
                }else if(order == 1){
                    s = {text:this.$L("已确认订单"),color:'green'}
                }else if(order == 0){
                    s = {text:this.$L("未领取"),color:'red'}
                }
                return s;
            },
            fromTypeFunc(n){
                let f;
                if(n == 1){
                    f = {text:this.$L("大转盘")}
                }else if(n == 2){
                    f = {text:this.$L("刮刮卡")}
                }
                return f;
            },
            SearchTimeFunc(time){
                if(time !== null){
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                }else{
                    this.dtCreateStart = '';
                    this.dtCreateEnd = '';
                }
                this.getData();
            },
            confirmFunc(id){
                const _self = this;
                let pid = id;
                _self.$confirm(_self.$L('是否确认收货?'), _self.$L('提示'), {
                    confirmButtonText: _self.$L('确定'),
                    cancelButtonText: _self.$L('取消'),
                    type: 'warning'
                }).then(() => {
                    _self.$api('updatePrizeStatus', {prizeId: pid}, function (reData) {
                        if (reData.flag === 1) {
                            _self.$message({
                                type: 'success',
                                message: _self.$L('确认收货成功!')
                            });
                            _self.loading = false;
                            _self.getData();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            console.log(reData);
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                        }
                    }, function (error) {
                        console.log(error);
                        //网络请求错误
                        _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), { //todo:msg还未更换语言包语言包
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                })
            },
            applyFunc(id,type,form,recordId,product_type){
                const _self = this;
                // let pid = id;
                _self.$confirm(_self.$L('是否申请领奖?'), _self.$L('提示'), {
                    confirmButtonText: _self.$L('确定'),
                    cancelButtonText: _self.$L('取消'),
                    type: 'warning'
                }).then(() => {

                    this.$store.commit('setDialogObj', {
                        name: this.$L('立即兑换'),
                        view: exchangeBox,
                        width: '1000px',
                        height: '600px',
                        data: {
                            id:id,
                            recordId:recordId,
                            ptype:type,
                            form:form,
                            product_type:product_type
                        }
                    });

                })
            },

        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";
    @import "../../../assets/css/common/lib";
    .prize{
        padding: 20px;
        .prize-head{
            position: relative;
            width: 100%;
            height: 45px;
            text-align: right;
            .font-middle{
                padding: 0 10px;
            }
            .right{
                float: right;
                .time-search{

                }
                .font{
                    padding-left: 20px;
                    font-size: 16px;
                }
                .search{
                    width: 120px;
                    padding-right: 10px;
                }
                .input-with-select{
                    width: calc(100% - 480px);
                    padding-left: 10px;
                }
            }
        }
        .prize-content{
            padding: 20px;
            background: #fff;
            .el-select{
                margin-right: 20px;
            }
            .prizeList{
                .table{
                    .img{
                        vertical-align: middle;
                        float: left;
                        width: 100px;
                        height: 100px;
                    }
                    .textBox{
                        width: 60%;
                        padding-left: 20px;
                        display: inline-block;
                        p:first-child{
                            font-size: 14px;
                            text-overflow: ellipsis;
                            display: -webkit-box;
                            -webkit-line-clamp: 2;
                            -webkit-box-orient: vertical;
                            overflow: hidden;
                        }
                    }
                    .red{
                        color: #FF2D52;
                    }
                    .green{
                        color: #00CC34;
                    }
                    .gray{
                        color: #999999;
                    }
                    .details{
                        margin-top: 10px;
                        cursor: pointer;
                    }
                }
            }
            .list-pages{
                text-align: center;
                padding: 40px 0 0;
            }
            /deep/ .el-table__empty-block {
                @extend .no-select;
                position: relative;
                width: 100%;
                height: 380px;
                line-height: 380px;
                text-align: center;
                background-color: #ffffff;
                font-size: 16px;
                color: #999;
                margin-bottom: 20px;
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>