<template>
    <div class="ex-chang-box" v-loading="loading">
        <iframe :src="url" class="windows" :style="{height:height}" ref="ifr" @load="onLoad"></iframe>
    </div>
</template>

<script>
    export default {
        name: "exchangeBox",
        inject: ['reload'],
        props: {
            height: {
                type: String,
                default: '',
            },
            loading: {
                type: Boolean,
                default: false
            },
            data: {
                type:Object,
                default: {}
            },
            listener: {
                type: Object,
                default: () => {
                    return {};
                }
            }
        },
        created(){

        },
        watch: {
            listener(newData, oldData) {
                if (newData && newData.hasOwnProperty('order')) {
                    this.exChangeListen(newData.order);
                }
            }
        },
        computed: {
            url: {
                get() {
                    let prize_from = this.data.hasOwnProperty('form') ? this.data.form : 0;
                    let ptype = this.data.hasOwnProperty('ptype') ? this.data.ptype : 0;
                    let lot_record_id = this.data.hasOwnProperty('recordId') ? this.data.recordId : 0;
                    let product_type = this.data.hasOwnProperty('product_type') ? this.data.product_type : 0;
                    return this.$C('serverUrlAjax') + "/exusers/get_prize.php?ptype="+ ptype +"&lot_record_id="+ lot_record_id +"&idweb=" + this.$getWebId() + "&prize_from="+prize_from+"+&lang=" + this.$lang()+"&product_type=" + product_type + "&v=9";
                }
            }
        },
        methods: {
            onLoad() {
                this.$refs.ifr.contentWindow.postMessage({msg: "isVue"}, this.$C('serverUrlAjax'));
            },
            exChangeListen(action, data) {
                switch (action) {
                    case 'reloadView':
                        this.$emit("closeDialog");
                        this.reload();
                        break;
                    case 'home':
                        this.$emit("closeDialog");
                        this.$router.push({
                            name: 'home',
                        });
                        break;
                }
            },
        },

    }
</script>

<style lang="scss" scoped>
    @import '../../assets/css/common/lib.scss';

    .ex-chang-box {
        position: relative;
        width: 100%;
        padding: 20px;

        .windows {
            position: relative;
            width: 100%;
        }
    }
</style>